

@import 'vars.scss';
.contact-header{
    padding-top: 0; 
}
.address-mobile{
    display: none;
}
.contact-main{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $dark;
    .contact-section1{
        display: flex;
        margin-top: 100px;
        .form{
            margin-right: 150px;
            h2{
                font-size: 48px;
                line-height: 56px;
                font-weight: bold;
                margin-bottom: 15px;
            }
            a{
                font-size: 15px;
                line-height: 28px;
                font-weight: bold;
                margin-bottom: 55px;
            }
            form{
                display: flex;
                flex-direction: column;
                align-items: center;
                padding-top: 55px;
                input{
                    display: block;
                    border: 0;
                    outline: 0;
                    font-size: 17px;
                    color: $dark;
                    width: 525px;
                    margin-bottom: 50px;
                    border-bottom: 2px solid $grey;
                    background-color: transparent;
                    padding: 0 10px 10px 0;
                    transition: .4s;
                    &::placeholder{
                        color: $grey;
                        font-size: 17px;
                    }
                    &:hover{
                        border-color: $dark;
                    }
                    &:invalid:not(:placeholder-shown){
                        border-color: #F45B53;
                    }
                    &:valid:not(:placeholder-shown){
                        border-color: #41CE7F;
                    }
                    &:focus{
                        border-color: $blue;
                    }
                    
                   
                }
                textarea{
                    resize: none;
                    width: 100%;
                    border: 0;
                    display: block;
                    box-sizing: border-box;
                    outline: 0;
                    font-size: 17px;
                    color: $dark;
                    border-bottom: 2px solid $grey;
                    transition: .4s;
                    &::placeholder{
                        color: $grey;
                    }
                    &:hover{
                        border-color: $dark;
                    }
                    &:invalid:not(:placeholder-shown){
                        border-color: #F45B53;
                    }
                    &:valid:not(:placeholder-shown){
                        border-color: #41CE7F;
                    }
                    &:focus{
                        border-color: $blue;
                    }
                }
                .button{
                    align-self: flex-start;
                    background-color: #fff;
                    border-color: $dark;
                    color: $dark;
                    margin-top: 50px;
                    cursor: pointer;
                    svg path{
                        stroke: $dark;
                    }
                    &:hover{
                        background-color: $dark;
                        color: white;
                        svg path{
                            stroke: white;
                        }
                    }
                }
                

            }
            
        }
    }
    .contact-section2{
        display: flex;
        margin-bottom: 100px;        
        margin-top: 140px;
        .map-side{
            margin-right: 60px;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        .adresses-row{
            display: flex;
            &:not(:last-child){
                margin-bottom: 75px;
            }
            .address:nth-child(2){
                margin-left: 40px;
            }
        }
    }
    .address{
        width: 300px;
        h5{
            font-size: 17px;
            line-height: 30px;
            text-transform: uppercase;
            font-weight: bold;
            margin-bottom: 15px;
        }
        p{
            line-height: 40px;
            font-size: 20px;
        }
    }
    .address-row1-russia{
        display: none;
    }
    .address-row2-neth{
        display: none;
    }
    .address-map-israel{
        display: none;
    }
    .contact-india{
        align-self: flex-end;
        margin-top: 140px;
        margin-bottom: 0;
        width: 270px;
    }
}
@media only screen and (max-width: 1400px) {
    .contact-main{
        .contact-section1{
            .form{
                form{
                    input{
                        width: 590px;
                    }
                }           
            }
            .form{
            margin-right: 50px;
            }
        }
        .contact-image{
            img{
                width: 600px;
            }
        }
        .contact-section2{
            .map-side{
                .map-image img{
                    width: 600px;
                }
                .contact-india{
                    margin-top: 230px;
                }
            }
            .adresses{
                .address200{
                    width: 200px;
                    p{
                        width: 200px;
                    }
                }

            }
        }
    }
}
@media only screen and (max-width: 1280px) {
    .contact-main{
        .contact-section1{
            margin-top: 100px;
            .form{
                width: 450px;
                h2{
                    font-size: 36px;
                    line-height: 55px; 
                    margin-bottom: 50px;
                }
                a{
                    font-size: 15px;
                    line-height: 28px;
                    font-weight: bold;
                    margin-bottom: 55px;
                }
                form{
                    input{
                        width: 100%;
                        box-sizing: border-box;
                    }
                    button{
                        font-size: 14px;
                    }
                }   
            }
            .contact-image{
                width: 450px;
                height: 660px;
                position: relative;
                overflow: hidden;
                img{
                    min-height:100%;
                    position: absolute;

                    left: 50%;
                    transform: translate(-50%);
                    width: 578px;
                }
            }
        }
        .contact-section2{
            display: flex;
            margin-bottom: 100px;        
            margin-top: 140px;
            .map-side{
                margin-right: 60px;
                display: flex;
                flex-direction: column;
                align-items: center;
                .map-image{
                    width: 480px;
                    img{
                        width: 100%;
                    }
                }
                .contact-india{
                    align-self: flex-end;
                    margin-top: 155px;
                    margin-bottom: 0;
                    width: 200px;
                }
            }
            .adresses-row{
                display: flex;
                width: 450px;
                &:not(:last-child){
                    margin-bottom: 75px;
                }
                .address:nth-child(2){
                    margin-left: 40px;
                }
            }
        }
        .address{
            width: 250px;
            h5{
                font-size: 17px;
                line-height: 30px;
                text-transform: uppercase;
                font-weight: bold;
                margin-bottom: 15px;
            }
            p{
                line-height: 26px;
                font-size: 16px;
            }
        }
        
    }
}
@media only screen and (max-width: 1024px) {
    .contact-main{
        .contact-section1{
            justify-content: space-between;
            .form{
                width: 350px;   
                form{
                    input::placeholder{
                        font-size: 14px;
                    }
                }
            }
            .contact-image{
                width: 350px;
            }
        }
        .contact-section2{
            flex-direction: column-reverse;
            width: 100%;
            .map-side{
                display: flex;
                align-items: flex-start;
                justify-content: space-around;
                margin-right: 0;
                flex-direction: row-reverse;
                .map-image{
                    width: 440px;
                }
                .contact-india{
                    margin-top: 0;
                    display: none;
                }
                .address-map-israel{
                    display: block;
                    width: 220px;
                }
            }
            .adresses{
                width: 100%;
                .adresses-row{
                    width: 100%;
                    justify-content: space-evenly;
                    .address-row1-russia, .address-row2-neth{
                        display: block;
                    }
                    .address{
                        width: 220px;
                        margin-left: 0;
                    }
                    
                    
                }
            }
            .adresses-row-russia-israel{
                display: none;
            }
        }
    }
}
@media only screen and (max-width: 767px){
    .address-mobile{
        display: block;
    }

    .contact-main{
        .contact-section1{
            flex-direction: column;
            margin-top: 0;
            width: 100%;
            align-items: center;
            .form{
                width: calc(100% - 60px);
                margin-right: 0;
                h2{
                    font-size: 32px;
                    line-height: 42px;
                    margin-bottom: 20px;
                }
                h2 + a{
                    font-size: 15px;
                    line-height: 28px;
                }
                form{
                    padding-top: 40px;
                    button{
                        width: 100%;
                        justify-content: center;
                        svg{
                            height: 11px;
                            margin-top: 5px;
                        }
                    }
                }
            }
            .contact-image{
                height: 30vw;
                min-height: 200px;
                width: 100%;
                margin-top: 40px;
                margin-bottom: 40px;
                img{
                    top: 50%;
                    width: 100%;
                    transform: translate(-50%, -60%);
                }
            }
        }
        .contact-section2{
            margin-top: 0;
            .adresses{
                background-image: url(../images/contact/map.svg);
                background-repeat: no-repeat;
                background-size: contain;
                background-position: 50% 70%;
                .adresses-row{
                    flex-direction: column;
                    align-items: center;
                    margin-bottom: 0;
                    &:nth-last-child(2){
                        display: none;
                    }
                    &:nth-last-child(1){
                        display: none;
                    }
                    .address{
                        width: 300px;
                        margin-bottom: 30px;
                    }
                    .mobile-none-address{
                        display: none;
                    }
                }
            }
            .map-side{
                .map-image{
                    display: none;
                }
                .address-map-israel{
                    width: 300px;
                    display: none;
                }
            }
        }
        
    }
}